@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

@layer base {
  body {
    @apply text-sm leading-5 tracking-normal font-normal;
    font-family: 'Poppins', sans-serif;
  }
}

@layer components {

  .header {
    @apply flex justify-center pt-5 relative;
    background: linear-gradient(180deg,#5c73f2 60%,#ffffff 0);
  }

  .me {
    @apply
      flex justify-center items-center
      w-[130px] h-[130px] md:w-[330px] md:h-[330px]
      bg-[url('./assets/me.png')] bg-cover bg-center
      border-4 md:border-8 border-white
      rounded-full transition-all;
  }

  .topic {
    @apply flex flex-col;
  }

  .topic .title {
    @apply flex items-center h-10 border-l-4 border-gray-400 pl-2 text-xl font-light
  }

  .topic .description {
    @apply ml-5 text-xs mb-3 last:mb-0
  }

  .socialLinks {
    @apply flex flex-row items-center;
  }
}

@media print { 
  .noprint {
    @apply hidden
  }
  .informations { @apply flex-row; }
  article { @apply w-1/2; }
}